.alert{
    background: red;
}
.timer_button{
    color: #fff;
    background: #5fb2c1;
    padding: 10px;
    border-style:none;
    border-radius: 5px;
    margin: 5px;
    text-decoration: none;
    /* 右に0px 下に4pxの影を配置 */
    box-shadow: 0 4px #3b8a98;

    /* inline要素、relativeに設定し、上に2pxボタンをずらす */
    display:inline-block;
    position:relative;
    top: -2px;
}

.timer_button:hover{
    /* 影を3px減らす */
    box-shadow: 0 1px #3b8a98;
    /* 通常時から相対的に下に3pxずらすことで
       ボタンが押された状態を表現 */
    top:1px;
}
